.is-hidden-sidebar {
  .sidebar {
    width: 5.5rem;
    &__collapse {
      font-size: 0; } }
  .nav {
    &__item {
      &.is-active {
        .subnav {
          display: none !important; } } }
    &__title {
      color: transparent;
      .icon-caret-d {
        opacity: 0; } } }
  .out {
    padding-left: 5.5rem;
    @include r($mob) {
      padding-left: 0; } }
  @include r($port) {
    .sidebar {
      width: 20.5rem;
      &__collapse {
        font-size: 1.6rem; } }
    .nav {
      &__item {
        &.is-active {
          .subnav {
            display: block !important; } } }
      &__title {
        color: #fff;
        .icon-caret-d {
          opacity: 1; } } } } }
html {
  @include r($port) {
    @include rmin($mob) {
      .sidebar {
        width: 5.5rem;
        &__collapse {
          font-size: 0;
          &.is-active {
            .icon {
              transform: rotate(0); } }
          .icon {
            transform: rotate(180deg); } } }
      .nav {
        &__item {
          &.is-active {
            .subnav {
              display: none !important; } } }
        &__title {
          color: transparent;
          .icon-caret-d {
            opacity: 0; } } }

      .out {
        padding-left: 5.5rem;
        @include r($mob) {
          padding-left: 0; } } } } }

.is-visible-sidebar {
  @include r($mob) {
    .sidebar {
      transform: translateX(0); } } }
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 9;
  width: 20.5rem;
  background: $main;
  @include agency;
  color: #fff;
  transition: transform, width, .2s;
  @include r($mob) {
    top: 6.5rem;
    right: 0;
    left: auto;
    transform: translateX(100%); }
  &__collapse {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding-left: 2.8rem;
    height: 6.3rem;
    background: none;
    font-size: 1.6rem;
    @include ptsans;
    color: #fff;
    white-space: nowrap;
    text-align: left;
    @include r($mob) {
      display: none; }
    .icon {
      margin-right: .5rem;
      fill: #fff;
      width: 7px;
      height: 11px; }
    &.is-active {
      padding-left: 2.4rem;
      .icon {
        transform: rotate(180deg); } } }
  &__scroll {
    position: absolute;
    top: 6.5rem;
    left: 0;
    right: 0;
    bottom: 0;
    padding-bottom: 7rem;
    overflow-y: auto;
    overflow-x: hidden;
    @include r($mob) {
      top: 0;
      padding: 0; } } }
.nav {
  line-height: 1;
  white-space: nowrap;
  &__item {
    transition: background .2s;
    min-height: 6rem;
    &.is-active {
      background: #e60000;
      .icon-caret-d {
        transform: rotate(-180deg); } }
    &.is-current {
      .subnav {
        display: block; } } }
  &__title {
    display: block;
    border-bottom: 1px solid #f33;
    position: relative;
    padding: 0 0 0 1.8rem;
    color: #fff;
    line-height: 6rem;
    font-size: 2.4rem;
    text-transform: uppercase;
    cursor: pointer;
    transition: color .2s; }
  .icon {
    position: relative;
    vertical-align: middle;
    margin-right: .5rem;
    width: 2.1rem;
    height: 2.1rem;
    fill: #fff;
    &-user {
      top: -2px;
      height: 2.4rem; }
    &-report {
      top: -4px;
      height: 2.8rem; }
    &-ball {
      top: -2px; }
    &-settings {
      top: -2px; }
    &-caret-d {
      position: absolute;
      right: 1.5rem;
      top: 50%;
      margin-top: -.3rem;
      width: 1.1rem;
      height: .6rem;
      transition: opacity, transform, .2s; } }
  &__logout {
    color: #f90;
    .icon {
      position: relative;
      top: -2px;
      fill: #f90;
      width: 21px;
      height: 24px; } }

  .subnav {
    display: none;
    padding: 0 0 1.3rem 0;
    @include ptsans;
    text-transform: capitalize;
    &__item {
      &.is-active {
        font-weight: 700; } }
    &__link {
      display: block;
      padding: .9rem 0 .9rem 4.8rem;
      color: #fff; } } }
