.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  padding: 0 20px;
  height: 6.5rem;
  line-height: 6.5rem;
  color: #fff;
  @include agency;
  text-transform: uppercase;
  background-image: linear-gradient(to bottom, #313131, #000);
  white-space: nowrap;
  @include clr;
  @include r($mob) {
    padding: 0 1.2rem; }
  &__left {
    float: left;
    .btn-action {
      margin-left: 2rem; } }
  &__right {
    float: right;
    .btn-main {
      margin-left: .6rem; } }
  .page-title {
    @include r($mob) {
      display: none; } } }

.logo {
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  margin: .5rem 3rem 0 0;
  text-shadow: 0.1rem 0.17rem 0.2rem rgba(0, 0, 0, 0.75);
  font-size: 3.6rem;
  color: #fff; }

.burger {
  position: absolute;
  right: 0;
  top: 0;
  width: 5.8rem;
  height: 6.5rem;
  line-height: 1;
  background: none;
  display: none;
  @include r($mob) {
    display: block; }
  &.is-active {
    i {
      background: none;
      &:before {
        transform: translateY(1rem) rotate(45deg); }
      &:after {
        transform: translateY(-1rem) rotate(-45deg); } } }
  i {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    height: .5rem;
    width: 3.4rem;
    background: #fff;
    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 0;
      height: .5rem;
      background: #fff;
      width: 100%;
      transition: transform .2s; }
    &:before {
      top: -1rem; }
    &:after {
      bottom: -1rem; } } }
