$link: #0b6cce;
$panel: #f5f5f5;
$table: #e9e9e9;
$border: #ddd;
$border-dark: #ccc;
$main: #f00;
//$main: #bb0202
$light: #999;
$dark: #333;
$port: 1023;
$mob: 767;

@mixin ptsans {
  font-family: "PT Sans", sans-serif; }

@include font(Agency-Bold, Agency-Bold);
@mixin agency {
  font-family: "Agency-Bold", Arial, sans-serif; }

@mixin input {
  width: 100%;
  height: 3.6rem;
  border: 1px solid #ccc;
  border-radius: 0.2rem;
  font-size: 1.6rem;
  color: #000;
  background: #fff; }
