// # SHORT HOWTO
// ## Sprites
//     .whatever
//          +s(codedby)
// ## gradient example
//      +g(#232323, #5c5c5c);
//     horizontal
//      +gh(#232323, #5c5c5c);
// ## making bw-images
//     img
//          @extend %gray
// ## predefined media-queries: https://github.com/paranoida/sass-mediaqueries
//     +r(320)
//          display: none
// +max-screen(1000), +iphone5, +iphone4, +ipad-retina, +ipad, +hdpi - also available
//
// ## use cycles
//     @for $i from 1 through 3
//       .item-#{$i}
//          width: 2em * $i;
//     @each $s in a,b,c
//          #{$s}
//              display: none
// ## fonts
// Save fonts to /src/fonts
// +font(book, cytiapro-black-webfont)
// and create mixin
// =book
//  font-family: "book", Arial, sans-serif

html {
  font-size: 62.5%;
  -webkit-font-smoothing: antialiased;
  @include selection {
    background: rgba(#000, .8);
    color: #fff; } }

body {
  min-width: 320px;
  font-size: 1.6rem;
  line-height: 1.4;
  @include ptsans;
  font-weight: 400; }

a {
  text-decoration: none; }

input,
button {
  font-family: inherit; }

@for $i from 1 through 12 {
  .col-pda-#{$i} {
    width: 100% / 12 * $i; } }

@for $i from 1 through 12 {
  .col-mob-#{$i} {
    @include r($mob) {
      width: 100% / 12 * $i; } } }

.row {
  margin: 0 -1.4rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  > div {
    padding: 0 1.4rem; } }

.hidden-mob {
  @include r($mob) {
    display: none !important; } }

.visible-mob {
  display: none !important;
  @include r($mob) {
    display: block !important; } }

.out {
  padding: 6.5rem 0 0 20.5rem;
  transition: padding .2s;
  @include r($mob) {
    padding-left: 0; } }

.panel-action {
  margin: 0 0 3rem;
  border-bottom: 1px solid $border-dark;
  padding: 1.7rem 4rem 2rem 2rem;
  background: $panel;
  @include r($mob) {
    margin: 0 0 3.8rem;
    padding: 1rem 1.2rem 2rem 1.2rem; }
  .field {
    &:last-child {
      margin-bottom: .8rem; } }
  .btn-main {
    @include r($mob) {
      margin-top: 1.2rem; } }
  &.is-active {
    + .table-wrap {
      margin-top: 0; }
    @include r($mob) {
      margin-top: 0; } }
  + .table-wrap {
    margin-top: -3.1rem;
    @include r($mob) {
      margin-top: -3.9rem; } } }

.title {
  margin: 0 0 1.8rem;
  color: #000;
  font-size: 2.6rem;
  font-weight: 700;
  line-height: 1.2;
  @include r($mob) {
    margin-bottom: .7rem; }
  &_inline {
    display: inline-block;
    vertical-align: middle;
    margin: 0; }
  &_table {
    margin: 1.2rem 0;
    padding: 0 2rem;
    font-size: 2rem;
    @include r($mob) {
      padding: 0 1.2rem; } }
  &_small {
    font-size: 2.4rem; }
  &_tiny {
    font-size: 1.6rem; } }

.page-head {
  position: relative;
  display: none;
  padding: 1.8rem 6rem 1rem 1.2rem;
  min-height: 6rem;
  background: $main;
  @include r($mob) {
    display: block; }
  .page-title {
    margin: 0;
    display: block;
    @include text-overflow; }
  .btn-action_add {
    position: absolute;
    right: 0;
    top: 0; } }

.page-title {
  display: inline-block;
  vertical-align: middle;
  line-height: 1;
  margin: .5rem 0 0;
  font-size: 3rem;
  @include agency;
  color: #fff;
  text-transform: uppercase;
  .icon {
    vertical-align: text-top;
    margin-right: 2px;
    fill: #fff;
    width: 2.3rem;
    height: 2.3rem; } }

.page-nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem 1.2rem;
  &_panel {
    background: $panel; } }

.message {
  position: relative;
  padding: 1.8rem 2rem 1.8rem 5rem;
  font-size: 1.2rem;
  background: $panel;
  color: $dark;
  @include r($mob) {
    padding: 1.2rem 1.2rem 1.2rem 4.5rem; }
  a {
    color: $link; }
  .icon {
    position: absolute;
    top: 2rem;
    left: 1.7rem;
    fill: #ccc;
    width: 2.1rem;
    height: 2.1rem;
    @include r($mob) {
      left: 1.2rem; } } }

.scheme {
  margin: 3rem 0 0;
  padding: 0 2.5rem;
  @include r($port) {
    padding: 0 1.5rem; }
  @include r($mob) {
    margin: 3.8rem 0 0; }
  &__row {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-around;
    margin: 0 0 5rem;
    @include r($mob) {
      margin: 0 0 3.2rem; }
    &:last-child {
      margin-bottom: 4rem;
      @include r($mob) {
        margin-bottom: 3rem; } } } }

.player {
  width: 25%;
  text-align: center;
  &__details {
    position: relative; }
  &__info {
    position: relative;
    display: inline-block;
    max-width: 100%;
    margin: 0 0 .6rem;
    padding-left: 1rem;
    font-size: 1.4rem;
    @include r($mob) {
      display: none; }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: .5rem;
      bottom: .5rem;
      width: 1px;
      background: $border-dark; }
    span {
      display: inline-block;
      padding: 0 5px;
      color: $border-dark; } }
  &__number {
    display: inline-block;
    position: relative;
    .icon {
      fill: #000;
      width: 8.3rem;
      height: 6.6rem;
      @include r($mob) {
        width: 6rem;
        height: 4.8rem; } }
    span {
      position: absolute;
      left: 0;
      right: 0;
      top: 50%;
      margin-top: -1.2rem;
      color: #fff;
      @include agency;
      font-size: 3.4rem;
      line-height: 1;
      @include r($mob) {
        font-size: 2.5rem; } }
    .counter {
      display: none;
      position: absolute;
      right: -1rem;
      top: -2rem;
      @include r($mob) {
        display: block; } } }
  &__data {
    text-align: left;
    @include text-overflow; }
  &__action {
    @include r($mob) {
      margin-top: .5rem; }
    .btn-action {
      @include r($mob) {
        padding: 0;
        font-size: 0;
        width: 3rem;
        height: 3rem;
        .icon {
          margin: 0;
          width: 1.8rem;
          height: 1.8rem; } } } } }

.player_goalkeeper {
  .player__number {
    .icon {
      fill: #00c498; } } }

.player_big {
  width: auto;
  .player__number {
    .icon {
      width: 13.2rem;
      height: 10.5rem;
      @include r($mob) {
        width: 8.3rem;
        height: 6.6rem; } }
    span {
      margin-top: -2rem;
      font-size: 5.4rem;
      @include r($mob) {
        margin-top: -1rem;
        font-size: 3.4rem; } } } }


.player_inline {
  display: inline-block;
  vertical-align: middle;
  margin-right: 1.5rem; }

.player-info {
  span {
    color: #ccc; } }

.table-wrap {
  position: relative;
  margin: 0 0 4rem;
  overflow-x: auto;
  overflow-y: hidden;
  transition: margin .2s;
  .ps-scrollbar-x-rail {
    opacity: 0 !important; }
  + .title {
    margin-top: -2.5rem; }
  + .btn-wrap_tiny {
    margin-top: -2.5rem; } }

.table {
  font-size: 1.6rem;
  @include ptsans;
  thead {
    background: $table; }
  th,
  td {
    min-width: 12rem;
    text-align: left;
    border: 1px solid $border;
    padding: 1.3rem 2rem;
    @include r($mob) {
      padding: 1.1rem 1rem 1.1rem 1.2rem; }
    &:first-child {
      border-left: 0; }
    &:last-child {
      border-right: 0;
      min-width: 18rem; }
    &:nth-child(3) {
      min-width: 20rem; } } }

.inner {
  padding: 2rem;
  @include r($mob) {
    padding: 2rem 1.2rem; } }
.panels-wrap {
  margin-bottom: 3.5rem; }

.panel {
  margin: 0 0 .5rem;
  border: 1px solid $border;
  border-radius: 2px;
  overflow: hidden;
  @include ptsans;
  &__head {
    position: relative;
    padding: 1.1rem 1.5rem;
    font-size: 1.6rem;
    background: $panel;
    &:after,
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background: #999; }
    &:after {
      right: 1.5rem;
      width: 11px;
      height: 3px; }
    &:before {
      right: 1.9rem;
      width: 3px;
      height: 11px;
      transition: height .2s; }
    &.is-active {
      &:before {
        height: 0; } } }
  &__body {
    border-top: 1px solid $border;
    padding: 1.5rem;
    font-size: 1.4rem; }
  &_collapse {
    .panel__body {
      display: none; } } }

.text {
  font-size: 1.4rem;
  a {
    color: $link; }
  p {
    margin-bottom: 1rem; }
  ul {
    margin-bottom: 1rem;
    padding-left: 4rem;
    list-style-type: disc; } }

.tabs {
  position: relative;
  margin: 0 0 2.5rem;
  border-bottom: 1px solid $border;
  &__item {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: -1px;
    border: 1px solid transparent;
    border-radius: 5px 5px 0 0;
    &.is-active {
      border-color: $border;
      border-bottom-color: #fff;
      .tabs__link {
        color: #000; } } }
  &__link {
    display: block;
    padding: .7rem 1.2rem;
    color: $link; } }

.date {
  font-size: 1.6rem; }

.btn-save-wrap {
    .field {
      &__body {
        @include r($mob) {
          margin-top: 2rem;
          text-align: center; } } } }

.counter {
  width: 4rem;
  height: 4rem;
  background: none;
  z-index: 2;
  font-size: 1.6rem;
  color: #fff;
  font-weight: 700;
  &:before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: -1;
    margin: -1.2rem 0 0 -1.2rem;
    width: 2.4rem;
    height: 2.4rem;
    border-radius: 50%;
    background: $main; } }
