.is-open-popup {
  overflow: hidden;
  body {
    overflow: hidden; }
  .popup {
    &.is-active {
      opacity: 1;
      visibility: visible; } } }

.popup {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 11;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity .2s;
  &_overlay {
    padding: 2.5rem;
    z-index: 9;
    background: rgba(#000,.6);
    .popup__in {
      padding: 1.5rem;
      width: 100%;
      background: $panel;
      font-size: 1.4rem; }
    .popup__close {
      padding: 1rem;
      .icon {
        width: 1.4rem;
        height: 1.4rem; } } }
  @include r($mob) {
    padding: 2rem; }
  @include rh(480) {
    padding: 6rem 2rem;
    align-items: flex-start; }
  &__in {
    position: relative;
    width: 51rem;
    @include r(540) {
      width: 100%; } }
  &__head {
    margin-bottom: 2.3rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    .title {
      padding-left: 1.5rem; } }
  &__close {
    position: absolute;
    padding: 2rem;
    right: 3rem;
    top: 3rem;
    background: none;
    @include r($mob) {
      right: 0;
      top: 0;
      padding: 2rem; }
    .icon {
      fill: #666;
      width: 2.9rem;
      height: 2.9rem; } }
  .field {
    margin-bottom: 1.8rem; }
  .btn-wrap {
    margin-bottom: 0; } }


