#ui-datepicker-div {
  display: none; }

.ui-datepicker {
  margin: 1.2rem 0;
  border: 1px solid $border;
  padding: .4rem;
  width: 26.2rem;
  font-size: 1.4rem;
  font-weight: 700;
  border-radius: 5px;

  &-header {
    position: relative;
    padding: .7rem 0;
    background: $main;
    color: #fff;
    border-radius: 5px; }
  &-prev,
  &-next {
    position: absolute;
    top: 0;
    height: 100%;
    line-height: 3.6rem;
    width: 4rem;
    &:before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 7px;
      height: 11px;
      background-image: url(../img/caret-l.svg); }
    span {
      display: none; } }
  &-prev {
    left: 0;
    padding-left: 1rem;
    text-align: left; }
  &-next {
    right: 0;
    padding-right: 1rem;
    text-align: right;
    &:before {
      transform: rotate(180deg); } }
  &-title {
    text-align: center;
    font-size: 1.6rem; }
  &-calendar {
    width: 100%;
    table-layout: fixed;
    border-spacing: 2px;
    border-collapse: separate;
    text-align: right;
    th {
      padding: .7rem 0;
      text-align: center; }
    td {
      border: 1px solid $border;
      padding: .4rem;
      text-align: right;
      background-image: linear-gradient(to bottom, #f9f9f9, #eee);
      color: $link;
      a {
        color: #000;
        transition: color .3s;
        &:hover {
          color: $link; } }
      &.ui-datepicker-other-month {
        opacity: 0; } }
    .ui-datepicker-current-day {
      background: none;
      border-color: $main; }
    .ui-datepicker-today {
      border-color: #000;
      background: none; } } }






