.field {
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  margin: 0 0 2rem;
  @include r($mob) {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 1.4rem; }
  &__title {
    padding-right: 1rem;
    width: 32%;
    font-size: 1.7rem;
    font-weight: 700;
    @include r($mob) {
      margin-bottom: .3rem;
      width: 100%;
      font-size: 1.6rem; } }
  &__body {
    width: 68%;
    @include r($mob) {
      width: 100%; } }
  &_full {
    flex-direction: column;
    .field__title,
    .field__body {
      width: 100%; } }
  &_equals {
    .field__title {
      width: 44%;
      font-size: 1.6rem;
      @include r($mob) {
        width: 100%; } }
    .field__body {
      width: 56%;
      @include r($mob) {
        width: 100%; } } } }

.input {
  display: block;
  padding: 0 1rem;
  width: 100%;
  @include input;
  transition: border-color .3s;
  @include placeholder {
    color: $light; }
  &:focus {
    border-color: $link; } }

.select {
  position: relative;
  display: block;
  &__head {
    display: block;
    padding: 0 1rem;
    line-height: 3.6rem;
    @include input; }
  select {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    min-width: 100%;
    min-height: 3.6rem;
    opacity: 0;
    font-size: 1.6rem; }
  .icon {
    position: absolute;
    top: 50%;
    right: 1.5rem;
    margin-top: -3px;
    width: 11px;
    height: 6px;
    fill: $light; } }

.btn-wrap {
  text-align: center;
  margin: 4.5rem 0;
  @include r($mob) {
    margin: 3rem 0 3.5rem; }
  &_small {
    margin: 3.5rem 0;
    @include r($mob) {
      margin: 2.5rem 0; } }
  &_tiny {
    margin: 1.5rem 0; } }

.btn {
  display: inline-block;
  padding: 1.5rem 2.7rem 1rem;
  background-image: linear-gradient(to top, black 0%, $dark 100%);
  border-radius: 2px;
  @include agency;
  font-size: 3rem;
  color: #fff;
  line-height: 1;
  text-transform: uppercase;
  @include r($mob) {
    padding: 1.2rem 2rem .7rem;
    font-size: 2.4rem; } }

.btn-action {
  display: inline-block;
  padding: .6rem 1.5rem .5rem;
  background: $main;
  border-radius: 2px;
  font-size: 1.4rem;
  font-weight: 700;
  color: #fff;
  line-height: 1;
  text-transform: none;
  text-align: center;
  @include ptsans;
  .icon-plus {
    vertical-align: middle;
    margin-right: .2rem;
    width: 1.1rem;
    height: 1.1rem;
    fill: #fff; }
  &_add {
    padding: 0;
    width: 6rem;
    height: 6rem;
    line-height: 6rem;
    .icon {
      margin: 0;
      width: 2.4rem;
      height: 2.4rem; } }
  &a {
    padding: .8rem 1.5rem; } }

.btn-main {
  display: inline-block;
  padding: 0 1.3rem;
  font-size: 1.6rem;
  font-weight: 400;
  height: 3.4rem;
  line-height: 3.4rem;
  color: #fff;
  border-radius: 2px;
  text-align: center;
  @include ptsans;
  text-transform: none;
  background-image: linear-gradient(to top, black 0%, $dark 100%);
  .icon {
    fill: #fff; }
  .icon-search {
    position: relative;
    top: 2px;
    margin-right: 5px;
    width: 1.5rem;
    height: 1.5rem; }
  .icon-caret-d {
    margin-left: 5px;
    width: 1.1rem;
    height: .7rem;
    transition: transform .2s; }
  .icon-filter {
    width: 1.6rem;
    height: 1.5rem; }
  .icon-plus {
    margin-right: .5rem;
    width: 1.1rem;
    height: 1.1rem; }
  span {
    display: none;
    &.is-hidden-active {
      display: inline-block; } }
  &.is-active {
    .icon-caret-d {
      transform: rotate(180deg); }
    span {
      &.is-hidden-active {
        display: none; }
      &.is-visible-active {
        display: inline-block; } } }
  &_small {
    padding: 0 .8rem;
    height: 3rem;
    font-size: 1.4rem; }
  &_icon {
    padding: 0;
    width: 3rem;
    height: 3rem;
    line-height: 3rem;
    .icon {
      margin: 0;
      vertical-align: middle; } } }

@keyframes rotating {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.btn-more {
  display: inline-block;
  padding: 0 3.3rem;
  height: 3.4rem;
  line-height: 3.4rem;
  background: $table;
  border-radius: 2px;
  color: #666;
  font-size: 1.6rem;
  @include ptsans;
  font-weight: 700;
  .icon {
    position: relative;
    top: -2px;
    margin-right: .5rem;
    vertical-align: middle;
    fill: #666;
    &-more {
      width: 12px;
      height: 12px; } }
  &.is-progress {
    .icon-more {
      animation: rotating 1s linear infinite; } } }

.btn-view-wrap {
  font-size: 0; }

.btn-view {
  display: inline-block;
  width: 3.2rem;
  height: 3rem;
  line-height: 3rem;
  text-align: center;
  border-radius: 2px;
  background: $table;
  color: #999;
  &.is-active {
    background-image: linear-gradient(to top, black 0%, $dark 100%);
    color: #fff;
    .icon {
      fill: #fff; } }
  &_text {
    width: 5.7rem;
    font-size: 1.4rem;
    @include ptsans; }
  .icon {
    fill: $light;
    width: 1.8rem;
    vertical-align: middle;
    &-list {
      height: 1.7rem; }
    &-calendar {
      height: 1.8rem; } } }

.checkbox {
  font-size: 1.6rem;
  font-weight: 700;
  input {
    vertical-align: middle;
    margin-right: .5rem; }
  &__text {
    display: inline-block;
    vertical-align: middle;
    @include nosel; } }
