.fc {
  .fc-widget-header {
    thead {
      background: $table; }
    th {
      padding: 1.1rem 0;
      font-weight: 700;
      font-size: 1.6rem; } }
  .fc-prev-button,
  .fc-next-button {
    background: none;
    border: 0;
    box-shadow: none;
    color: $link;
    &:before {
      content: '';
      display: inline-block;
      vertical-align: middle; } } }
